body {
  overflow: hidden;  // prevent scroll
}

.App {
  text-align: center;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("http://nebula.wsimg.com/a5a68de31cf77282dea50becb1856094?AccessKeyId=D880AE3E6A86A26108BD&disposition=0&alloworigin=1");
    filter: grayscale(80%) blur(4px);
    z-index: -10;
  }
}

canvas {
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); /* mobile webkit */
}
